* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

body {
  font-family: montserrat;
  position: relative;
}

.container {
  max-width: 1160px;
  margin: 0px auto;
}

.page {
  display: grid;
  grid-template-rows: auto 1fr auto;
  position: relative;
}

.main__guests .select {
  width: 320px;
  height: 44px;
  position: relative;
}

.main__guests .select .select__header {
  border: 1px solid rgba(31, 32, 65, 0.25);
  cursor: pointer;
  display: flex;
}

.main__guests .select .select__header .select__current {
  font-size: 14px;
  line-height: 18px;
  font-family: montserrat;
  color: rgba(31, 32, 65, 0.75);
  appearance: none;
  padding-top: 13px;
  padding-bottom: 13px;
  padding-left: 15px;
}

.main__guests .select .select__header .select__icon {
  align-items: center;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  height: 40px;
  margin-left: auto;
  text-align: center;
  color: rgba(31, 32, 65, 0.75);
}

.main__guests .select .select__body {
  display: none;
  border: 1px solid rgba(31, 32, 65, 0.25);
  border-top: 0;
  left: 0;
  right: 0;
  top: 100%;
  position: absolute;
  background-color: #fff;
}

.main__guests .select .select__body .select__item {
  cursor: pointer;
  font-size: 16px;
  line-height: 24px;
  padding: 8px;
}

.main__guests .select .select__body .select__item:hover {
  background-color: #f2f2f2;
}

.main__guests .select.isActive .select__body {
  display: block;
}

