.burger {
  display: none;
  z-index: 7;
  position: absolute;
  top: 50%;
  left: 50%;
}

.burger li {
  font-size: 14px;
  line-height: 17px;
}

.burger li:nth-child(2) img {
  width: 15px;
  height: 12px;
  position: relative;
  top: 3px;
  margin-left: 5px;
}

.burger li:nth-child(5) img {
  width: 15px;
  height: 12px;
  position: relative;
  top: 3px;
  margin-left: 5px;
}

.burger li a {
  color: rgba(31, 32, 65, 0.5);
}

.canvas {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 4;
}

._active__burger.burger {
  display: block;
}

._active__burger.canvas {
  display: block;
  background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
}

.header {
  padding: 0 10px;
  height: 70px;
  background-color: #fff;
  position: fixed;
  width: 100%;
  z-index: 5;
}

.header__wrapper {
  margin-top: 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__name {
  display: flex;
  align-items: center;
}

.header__name img:first-child {
  width: 40px;
  height: 40px;
}

.header__name img:last-child {
  width: 53.19px;
  height: 40px;
}

.header__nav ul {
  display: flex;
  align-items: center;
}

.header__nav ul li {
  font-size: 14px;
  line-height: 17px;
}

.header__nav ul li button {
  border-radius: 22px;
  padding: 9px 20px 10px 20px;
  background-color: #fff;
  border: 3px solid #bc9cff;
}

.header__nav ul li button span {
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #bc9cff;
  font-family: montserrat-bold;
}

.header__nav ul li button:hover {
  background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
}

.header__nav ul li button:hover span {
  color: #fff;
}

.header__nav ul li:not(:last-child) {
  padding-right: 20px;
}

.header__nav ul li a {
  color: rgba(31, 32, 65, 0.5);
  font-size: 14px;
  line-height: 17px;
}

.header__adap i {
  background-color: #fff;
  color: #bc9cff;
}

.header__adap i:hover {
  color: #fff;
  background-color: #bc9cff;
  padding: 7px;
}

.header__adap .adap__link {
  display: none;
  position: absolute;
  top: 0;
  left: 15px;
}

.header__adap .adap__back {
  display: none;
}

.header__adap .adap__back .adap__enter {
  position: absolute;
  top: 5px;
  right: 15px;
}

.header__adap .adap__back .adap__reg {
  position: absolute;
  top: 5px;
  right: 85px;
}

@media (max-width: 992px) {
  .header__wrapper {
    justify-content: space-around;
  }
  .header__enter {
    display: none;
  }
  .header__reg {
    display: none;
  }
  .header__adap .adap__back {
    display: block;
  }
  .header__nav ul {
    position: relative;
    right: 65px;
  }
}

@media (max-width: 768px) {
  .header__wrapper {
    justify-content: center;
    position: relative;
  }
  .header__adap .adap__link {
    display: block;
  }
  .header__nav {
    display: none;
  }
}

.main {
  background-image: url("../../img/image3.png");
  background-size: cover;
  height: 100vh;
  padding-top: 90px;
}

.main__wrapper {
  position: relative;
}

.main__modal {
  position: absolute;
  top: 5vmax;
  left: 11vmax;
  background-color: #fff;
}

.main__modal form {
  padding: 40px 30px 30px 30px;
  display: grid;
  align-items: end;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  grid-template-areas: "title title" "arrival departure" "guests guests" "choice choice";
}

.main__title {
  font-family: quicksand-bold;
  font-weight: 700;
  grid-area: title;
  font-size: 24px;
  line-height: 30px;
  color: #1f2041;
  align-self: center;
}

.main__block p {
  font-family: montserrat-bold;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  margin-bottom: 5px;
}

.main__arrival {
  grid-area: arrival;
}

.main__departure {
  grid-area: departure;
}

.main__guests {
  grid-area: guests;
}

.choice {
  grid-area: choice;
  padding-top: 31px;
}

.choice button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 320px;
  height: 44px;
  text-align: center;
  text-transform: uppercase;
  font-family: montserrat-bold;
  background-color: #fff;
  color: #bc9cff;
  border-radius: 22px;
  padding: 14px 22px 15px 18px;
  border: 3px solid #bc9cff;
}

.choice button:hover {
  background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
  color: #fff;
}

.choice button p {
  font-size: 12px;
  line-height: 15px;
  display: inline-block;
  margin: 0px auto;
}

.choice button i {
  font-size: 20px;
}

@media (max-width: 768px) {
  .main__modal {
    position: static;
    display: inline-block;
    margin: 0 10px;
  }
}

